body {
  margin: 0;
  padding: 0;
  font-family: "Courier New", Courier, monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(left, rgb(204, 166, 235), rgb(81, 220, 137));
  background: linear-gradient(to right, rgb(204, 166, 235), rgb(81, 220, 137));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Tilt {
    background: -webkit-linear-gradient(left, rgb(204, 166, 235), rgb(81, 220, 137));
    background: linear-gradient(to right, rgb(204, 166, 235), rgb(81, 220, 137));
}
.form {
    width: 700px;
    background-color: transparent;
}
.bounding-box {
   position: absolute;
   box-shadow: inset 0 0 0 3px #149df2;
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   cursor: pointer;
}
.App {
  text-align: center;
}

button {
  cursor: pointer;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}
